.block-contacts {
  padding: 76px 0 72px;
  color: white;
  background: $midnight-blue;

  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    @media screen and (max-width: 992px) {
      display: block;
    }
  }

  &__title {
    margin-bottom: 35px;
    @include futurabold;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
  }

  &__list {
    padding-top: 30px;
    font-size: 16px;
    @media screen and (max-width: 992px) {
      padding-top: 0;
    }

    li {
      margin-bottom: 10px;
    }

    a {
      text-decoration: none;
      color: white;
    }
  }
}

.form-group {
  display: flex;
  margin-bottom: -1px;
  border: 1px solid $gray;
  @media screen and (max-width: 576px) {
    display: block;
  }

  &:last-child {
    margin-bottom: 26px;
  }

  input {
    flex: 1;
    @include placeholder-input(#8090B4, center);
  }

  input[type="text"],
  input[type="email"],
  textarea {
    font-size: 18px;
    color: white;
    text-indent: 15px;
    border: none;
    background: transparent;

    &:focus {
      border-color: white;
      outline: 1px solid white;
    }
  }

  input[type="text"],
  input[type="email"] {
    height: 76px;
    @media screen and (max-width: 576px) {
      display: block;
      width: 100%;
    }
  }

  &--flex {
    input[type="text"] {
      border-right: 1px solid $gray;
      @media screen and (max-width: 576px) {
        border: none;
        border-bottom: 1px solid $gray;
        width: 100%;
      }
    }
  }

  textarea {
    padding-top: 30px;
    width: 100%;
    height: 170px;
    @include placeholder-textarea(#8090B4, center);
  }
}

.form-footer {
  display: flex;
  align-items: center;
  margin-top: 26px;
  @media screen and (max-width: 992px) {
    margin-bottom: 80px;
  }
  @media screen and (max-width: 576px) {
    display: block;
  }


}

input[type="submit"] {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  width: 140px;
  height: 76px;
  @include recoleta;
  font-size: 24px;
  color: $midnight-blue;
  background: $gold;
  border: none;
  transition: all .3s;
  @media screen and (max-width: 576px) {
    margin-bottom: 10px;
  }


  &:hover {
    background: darken($gold, 10%);
    cursor: pointer;
  }
}

.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}

.checkbox + label {
  position: relative;
  cursor: pointer;
  display: inline-block;
  margin-bottom: 0;
  padding-left: 30px;
  vertical-align: middle;
  text-transform: none;
}

.checkbox + label:before {
  content: '';
  position: absolute;
  top: 7px;
  left: 0;
  margin-right: 16px;
  width: 20px;
  height: 20px;
  display: inline-block;
  border: 1px solid white;
  border-radius: 5px;
  transition: .2s;
  @media screen and (max-width: 1830px) {
    top: -3px;
  }
  @media screen and (max-width: 830px) {
    top: 7px;
  }

}

.checkbox + label:after {
  content: '\f00c';
  position: absolute;
  font-family: "Font Awesome 5 Pro";
  top: 7px;
  left: 3px;
  width: 22px;
  height: 22px;
  font-size: 16px;
  color: white;
  transition: .2s;
  opacity: 0;
  @media screen and (max-width: 1830px) {
    top: -3px;
  }
  @media screen and (max-width: 830px) {
    top: 7px;
  }
}

.checkbox:checked + label:after {
  opacity: 1;
}

.privacy {
  color: $gray;

  a {
    color: white;
    text-decoration: none;
  }
}
.copyright {
  padding: 20px 0;
  color: $wild-blue-yonder;
  background: $midnight-blue;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: 992px) {
      display: block;
    }

  }
  &__text {
    display: inline-block;
    max-width: 470px;
    a {
      text-decoration: none;
      color: white;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    @media screen and (max-width: 576px) {
      display: block;
      margin-bottom: 10px;
    }
  }
  &__right {
    display: inline-block;
    max-width: 165px;
  }
  img {
    margin-left:  24px;
    @media screen and (max-width: 576px) {
      display: block;
      margin-left: 0;
      margin-top: 20px;
    }

  }
}
.map iframe {
  display: block;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,.4);
  z-index: 10000;
}
