.main-header {
	position: relative;
	padding: 0 15px;
	.logo {
		padding: 54px 0 35px;
		text-align: center;
	}
}
.main-menu {
	display: flex;
	justify-content: center;
	align-items: center;
	@media screen and (max-width: 992px) {
		display: none;
	}
	@media screen and (min-width: 992px) {
		display: flex !important;
	}

	li.current-menu-item {
		a {
			color: $midnight-blue;
			&::after {
				background: $midnight-blue;
			}
		}
	}
	a {
		position: relative;
		display: block;
		margin: 0 20px;
		padding: 20px 0;
		@include futuramedium;
		font-size: 14px;
		text-transform: uppercase;
		text-decoration: none;
		color: $wild-blue-yonder;
		&:hover {
			color: $midnight-blue;
			&::after {
				background: $midnight-blue;
			}
		}
		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 3px;
			background: transparent;
			transition: all .3s;
		}
	}
}

