html,
body {
  font-size: 17px;
}

body {
  margin: 0;
  padding: 0;
  //padding-bottom: 700px;
  @include futuraregular;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.4;
  color: $midnight-blue;
  background: #fff;
  &.hide {
    overflow: hidden;
    padding-right: 14px;
  }
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  @include reset-list;
}

p {
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
}

@media screen and (max-width: $xl) {
  .container-big {
    width: 768px;
  }
}

@media screen and (max-width: $md) {
  .container-big {
    padding: 0 30px;
    width: 320px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1320px;
}

.section {
  padding-top: 16px;
  @include border-top;

  &__title {
    margin-bottom: 34px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: $midnight-blue;

    span {
      display: inline-block;
    }

    &--light {
      margin-right: 8px;

      @include futuralight;
    }

    &--bold {
      @include futurabold;
    }
  }
}

.count-1 {
  column-count: 1;
}

.count-2 {
  column-count: 2;
}

.count-3 {
  column-count: 3;
}

.count-4 {
  column-count: 4;
}

.read-more {
  @include recoletamedium;
  font-size: 18px;
  text-decoration: none;
  color: $midnight-blue;
  transition: all .3s;

  &:hover {
    color: $gold;
  }
}
.big {
  &__subtitle {
    margin-bottom: 14px;
   @include futurabold;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 0.1em;
  }
  &__title {
    margin-bottom: 20px;
    @include recoleta;
    font-size: 48px;
    text-align: center;
    @media screen and (max-width: 768px) {
      font-size: 32px;
    }

  }
  &__text {
    @include futuralight;
    font-size: 18px;
  }
  &.big--center {
    .big__text {
      margin-bottom: 50px;
      text-align: center;
    }
  }
}
