.socials-list {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  a {
    &:hover {
      svg {
        fill: $gold;
      }
    }
  }
  svg {
    display: inline-block;
    margin-right: 10px;
    width: 20px;
    height: 20px;
    transition: all .3s;
    fill: $midnight-blue;
  }
  .icon-mail {
    position: relative;
    top: 3px;
  }
}
