.about {
  &-picture {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: flex-end;
    grid-gap: 70px;
    margin-bottom: 56px;
    @media screen and (max-width: 992px) {
      display: block;
    }

    .big__title {
      text-align: left;
    }
    .big__subtitle {
      text-align: left;
    }
    .big__text {
      @media screen and (max-width: 992px) {
        margin-bottom: 40px;
      }
    }
    img {
      display: block;
    }
    &__item {
      .about-blocks__item {
        margin-top: 50px;
        .about-blocks__title {
          height: auto;
        }
      }
    }
  }
  &-blocks{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin-bottom: 86px;
    @media screen and (max-width: 992px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 768px) {
      display: block;
    }

    &__item {
      display: flex;
      flex-direction: column;
      padding: 40px;
      color: white;
      background: $midnight-blue;
      @media screen and (max-width: 768px) {
        margin-bottom: 40px;
      }

      p {
        margin-bottom: 20px;
      }
      a {
        display: flex;
        align-items: center;
        margin-top: auto;
        text-decoration: none;
        color: $gold;
        i {
          margin-right: 8px;
        }
      }
    }
    &__title {
      margin-bottom: 30px;
      height: 66px;
      overflow: hidden;
      @include recoleta;
      font-size: 30px;
      line-height: 1.1;
      @media screen and (max-width: 768px) {
        height: auto;
      }
    }
  }
}