.intro {
	margin-bottom: 74px;
	height: 670px;
	background-size: cover;
	background-position: center;
	@media screen and (max-width: 768px) {
		height: 400px;
	}
	@media screen and (max-width: 400px) {
		height: 340px;
	}

		.container {
		position: relative;
		height: 100%;
	}
	&__content {
		position: absolute;
		padding: 0 15px;
		left: 0;
		bottom: 75px;
	}
	&__subtitle {
		margin-bottom: 28px;
		text-transform: uppercase;
		letter-spacing: 1.4px;
		color: white;
	}
	&__title {
		@include recoletamedium;
		font-size: 64px;
		line-height: 1.1;
		letter-spacing: -1.92px;
		color: white;
		@media screen and (max-width: 768px) {
			font-size: 45px;
		}

		@media screen and (max-width: 400px) {
			font-size: 32px;
		}
	}
}
