.profile {
  margin-bottom: 130px;
  .section__title {
    margin-bottom: 52px;
  }
  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    @media screen and (max-width: 992px) {
      display: block;
    }

  }
  &__img {
    @media screen and (max-width: 992px) {
      margin-bottom: 60px;
    }

  }
  &__content {
    p {
      margin-bottom: 20px;
    }
    strong {
      @include futurabold;
    }
  }

}