.members {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 40px;
  grid-row-gap: 60px;
  justify-content: center;
  margin-bottom: 90px;
  padding-top: 60px;
  @media screen and (max-width: 992px) {
  	grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 576px) {
    display: block;
  }

  .socials-list {
    justify-content: flex-start;
  }
}
.members__title {
  margin-bottom: 20px;
  @include recoleta;
  font-size: 29px;
  @media screen and (max-width: 576px) {
    font-size: 22px;
  }

}
.members__subtitle {
  margin-bottom: 16px;
  font-size: 14px;
}