.block-services {
  margin-bottom: 96px;
  @media screen and (max-width: 992px) {
    margin-bottom: 0;
  }

  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    @media screen and (max-width: 992px) {
      display: block;
    }

  }
  &__item {
    background: $midnight-blue;
    @media screen and (max-width: 992px) {
      margin-bottom: 40px;
    }

  }
  &__img {
    position: relative;
    height: 262px;
    background-size: cover;
    background-position: center;
  }
  &__text {
    position: absolute;
    bottom: 30px;
    right: 30px;
    left: 30px;
    color: white;
    p {
      @include futuramedium;
      line-height: 1.2;
    }
  }
  &__title {
    @include recoleta;
    font-size: 40px;
    letter-spacing: -0.02em;
    @media screen and (max-width: 992px) {
      font-size: 30px;
    }
  }
  &__content {
    padding: 26px 50px 37px 30px;
    @include futuramedium;
    color: white;
    ul {
      column-gap: 30px;
      @media screen and (max-width: 1300px) {
      	column-count: 2;
      }
      @media screen and (max-width: 768px) {
      	column-count: initial;
      }

    }
    li {
      position: relative;
      padding-left: 14px;
      font-size: 14px;
      &::before {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: white;
      }
    }
  }
}