.lists {
  margin-bottom: 100px;
  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 70px;
    @media screen and (max-width: 768px) {
      display: block;
    }

  }
  &__item {
    padding-top: 16px;
    border-top: 1px solid rgba(0, 33, 105, 0.25);
  }
  li {
    position: relative;
    margin-bottom: 14px;
    padding-left: 14px;
    font-size: 17px;
    &::before {
      content: '';
      position: absolute;
      top: 10px;
      left: 0;
      width: 5px;
      height: 5px;
      background: $midnight-blue;
      border-radius: 50%;
    }
  }
}