.staffs {
  &__wrap {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    @media screen and (max-width: 1300px) {
    	grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 768px) {
      display: block;
    }
  }
  &__item {
    @media screen and (max-width: 768px) {
      margin-bottom: 40px;
    }
  }
  &__img {
    margin-bottom: 36px;
    height: 540px;
    background-size: cover;
    background-position: top center;
    border-bottom: 2px solid #D3DBED;
  }
  &__title {
    margin-bottom: 14px;
    @include recoleta;
    font-size: 36px;
    text-align: center;
  }
  &__text {
    margin: 0 auto 16px;
    max-width: 340px;
    text-align: center;
  }
}