.for-proffesionals {
  padding: 60px 0 100px;
  ul {
    display: flex;
    flex-wrap: wrap;
    //column-count: 2;
    @include futurabold;
    font-size: 14px;
    li {
      position: relative;
      margin-right: 18px;
      margin-bottom: 10px;
      padding-left: 10px;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 7px;
        width: 4px;
        height: 4px;
        background: $midnight-blue;
        border-radius: 50%;
      }
    }
  }
}
.for-proffesionals__wrap {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  @media screen and (max-width: 768px) {
    display: block;
  }
}
.for-proffesionals__item {
  margin-bottom: 30px;
}
.for-proffesionals__title {
  margin-bottom: 26px;
  @include recoleta;
  font-size: 30px;
  line-height: 1.2;
  @media screen and (max-width: 576px) {
    font-size: 26px;
  }

}
