.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 10001;
  &.modal--big {
    .modal__content {
      height: 680px;
      overflow-y: scroll;
    }
  }

  &__wrap {
    padding: 65px 50px 50px;
    width: 1060px;
    color: white;
    background: $midnight-blue;
    @media screen and (max-width: 1120px) {
      width: 700px;
    }
    @media screen and (max-width: 768px) {
      width: 400px;
    }
    @media screen and (max-width: 576px) {
      padding: 60px 20px;
      width: 280px;
    }
  }

  &__header {
    padding-bottom: 35px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);

    p {
      @include recoleta;
      font-size: 16px;
    }
  }

  &__title {
    margin-bottom: 15px;
    @include recoleta;
    font-size: 36px;
    @media screen and (max-width: 768px) {
      font-size: 24px;
    }
  }

  &__content {
    padding-top: 35px;
    color: #ffffffbf;
    @media screen and (max-width: 1120px) {
      height: 598px;
      overflow-y: scroll;
    }

    @media screen and (max-width: 576px) {
      height: 300px;
      overflow-y: scroll;
    }

    p {
      margin-bottom: 8px;
    }
  }

  &__text {
    margin-bottom: 30px;
    font-size: 16px;
  }
  &__small-text {
    font-size: 12px;
  }

  &__close {
    position: absolute;
    display: block;
    top: 5%;
    right: 5%;
    width: 24px;
    height: 24px;
    //border: 1px solid red;
    &::before,
    &::after {
      content: '';
      position: absolute;
      top: 0;
      width: 36px;
      height: 2px;
      background: $gold;
    }

    &::before {
      left: 0;
      transform-origin: top left;
      transform: rotate(45deg);
    }

    &::after {
      right: 0;
      transform-origin: top right;
      transform: rotate(-45deg);
    }
  }
  ul {
    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 14px;
      &::before {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 4px;
        height: 4px;
        background: rgba(255,255,255, .75);
        border-radius: 50%;
      }
    }
  }
}

.modal__columns {
  display: grid;
  grid-gap: 50px;
  font-size: 16px;
  color: rgba(255,255,255,.75);
  @media screen and (max-width: 768px) {
    display: block;
  }
  &.modal_columns__3 {
    grid-template-columns: repeat(3, 1fr);
  }
  &-title {
    margin-bottom: 20px;
    @include recoleta;
    font-size: 24px;
    line-height: 1;
    color: white;
  }
  strong {
    font-weight: bold;
  }
}