.partners {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 100px;
  grid-row-gap: 65px;
  padding-top: 80px;
  @media screen and (max-width: 992px) {
  	grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 576px) {
    display: block;
    padding-top: 30px;
  }
  &__item {
    @media screen and (max-width: 576px) {
      margin-bottom: 40px;
    }
  }

  &__img {
    margin-bottom: 16px;
    height: 96px;
  }
  &__title {
    margin-bottom: 16px;
    @include recoleta;
    font-size: 29px;
    @media screen and (max-width: 576px) {
      font-size: 22px;
    }

  }
}
